div.intro-background {
    background-color: #3E424D;
    min-height: 85vh;
    width: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

div.container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

div.intro {
    order: 2;
    padding: 2rem 0 5rem 2rem;
    min-width: 340px;
}

div.arrowContainer {
    height: 15vh;
    background-color: #3E424D;
    font-size: 40px;
    color: white;
}

a {
    text-decoration: none;
}

.arrow {
    position: relative;
    bottom: -2rem;
    left: 50%;
    margin-left: -20px;
    color: white;
}

.bounce {
    animation: bounce 2s infinite;
}

h2.typing {
    min-width: 350px;
}

div.headshot-image {
    order: 1;
    max-width: 30rem;
    text-align: right;
}

img.headshot-round {
    max-width: 20rem;
    padding: 5px;
    margin: 1rem;
    border-radius: 50%;
    border: 5px solid #BBCB9F;
}

h1, h3, span.typewrite .greenLarge{
    color: #9FC5CE;
    font-weight: 600;
    font-size: 6vh;
}

h3 {
    margin-bottom: 3rem;
    font-size: 38px;
}

.green {
    color: #BBCB9F;
}

Button.projects {
    margin-top: 1rem;
    font-family: "Ubuntu", sans-serif;
    background-color: #9FC5CE;
    text-decoration: none;
}

/* typwriter Descriptor */

.typewrite {
    color: #BBCB9F;
    -webkit-text-decoration-color: #9FC5CE;
            text-decoration-color: #9FC5CE;
    font-size: 3vh;
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto;
    -webkit-text-decoration-color: #BBCB9F;
            text-decoration-color: #BBCB9F;
    font-size: 3vh;
}

/* The typing effect */

@keyframes typing {
    from {
        width: 0
    }
    to {
        width: 100%
    }
}

/* The typewriter cursor effect */

@keyframes blink-caret {
    from, to {
        border-color: transparent
    }
    50% {
        border-color: #faf9f9;
    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-30px);
    }
    60% {
        transform: translateY(-15px);
    }
}

@media screen and (max-width: 800px) {
    div.container {
        flex-direction: column;
    }
    div.intro-background, div.headshot-image {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
    div.intro-background {
            min-height: 75vh;
    }
    div.arrow-container {
        min-height: 5vh;
    }
    img.headshot-round {
        width: 50vw;
    }
    div.intro {
        padding:0;
    }
}
#bio {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    padding: 5rem 0 5rem 0;
}

h3.about-title {
   margin-top: 0;
}

p.bio-content, div.contact-info {
    font-family: "Open Sans", sans-serif;
    max-width: 50vw;
    padding: 0 0 0 5rem;
}
div.skillsContainer {
    padding: 0 0 4rem 0;
}

h3.skillsTitle {
    text-align: center;
    margin: 20px 0 50px 0;
}

.blue {
    color: #457b9d;
}

.skillsSection {
    display: flex;
    flex-direction: row;
    padding: 0 0 5rem 0;
    justify-content: center;
}

div.skillsSection:first-of-type {
    padding: 0;
}

div.border {
    border-bottom: solid 5px #BBCB9F;
}

div.skillsList {
    width: 25rem;
    text-align: center;
    padding: 1rem;
}

span.blue {
    font-size: 50px;
    padding: 10px;
    color: #9FC5CE;
}

div.skillsList:first-of-type {
    border-right: solid 5px #BBCB9F;
}

div.skillsList p {
    font-size: 20px;
}

div.skillsList h3 {
    margin-bottom: 0;
}
#feedback {
    padding: 4rem 0 4rem 0;
    background-color: #9FC5CE;
}

h3.feedbackTitle {
    color: #3E424D;
    text-align: center;
    margin-top: 0;
}

p.feedback {
    font-size: medium;
    font-weight: 600;
    font-style: italic;
    text-align: center;
    margin: 0 20vw 0 20vw;
}
div.footer {
    background-color: #3E424D;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 20vh;
}

div.btnsRow {
    display: flex;
    flex-direction: row;
    align-items: center;
}

span.footer-btns {
    margin: 1vw 2vw 0 2vw;
    color: white;
    font-size: 35px;
}

p.footer-name {
    color: white;
    margin-top: 4vh;
}

@media screen and (max-width: 800px) {
    div.footer{
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        margin: 10px;
    }
    span.footer-btns {
        font-size: 25px;
    }
    p.footer-name {
        margin-top: 0;
    }
}
article.portfolio-item {
    display: flex;
    margin: 0 10vw 0 10vw;
}

h2.port-title {
    font-size: 30px;
    text-align: center;
}

div.text {
    text-align: justify;
    order: 2;
}

p.description {
    font-size: 18px;
}

p.tech {
    font-size: 18px;
}

div.image {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
    float: auto;
}

img.port-img {
    width: 30vw;
    min-width: 400px;
}

h2 {
    font-size: 24px;
    font-weight: 600;
}

Button.site {
    margin: 20px;
    background-color: cadetblue;
}

a:hover {
    text-decoration: none;
}

div.btnsRow {
    margin-top: 1rem;
}

@media screen and (max-width: 1000px) {
    article.portfolio-item {
        display: flex;
        flex-direction: column;
        margin: 0;
    }
}
header {
    text-align: center;
    height: 20vh;
    width: 100vw;
    background-color: #3E424D;
    display: flex;
   justify-content: center;
}

div.portfolio {
    margin-top: 2rem;
}

h1.portfolio-title {
    align-self: flex-end;
    text-align: center;
}

article.portfolio-item {
    padding: 2rem;
}
article.portfolio-item:nth-of-type(odd) {
    flex-direction: row-reverse;
    text-align: right;
}

article.portfolio-item:nth-of-type(odd)>h2 {
    text-align: right;
}

article.portfolio-item:nth-of-type(even)>div.text {
    padding-left: 2vw;
}

article.portfolio-item:nth-of-type(odd)>div.text {
    padding-right: 2vw;
}

@media screen and (max-width: 900px) {
    article.portfolio-item {
        display: flex;
        flex-direction: column;
        margin: 0;
    }

    article.portfolio-item:nth-of-type(even)>div.text {
        padding-left: 0;
    }
    
    article.portfolio-item:nth-of-type(odd)>div.text {
        padding-right: 0;
    }

    article.portfolio-item:nth-of-type(odd) {
        flex-direction: column;
    }
}
